import React from "react";
import {
	Container,
	Heading,
	Tabs,
	TabList,
	TabPanels,
	Tab,
	TabPanel,
	Stack,
	Center,
	Button,
	Text,
	Spinner,
	SimpleGrid,
} from "@chakra-ui/react";
import axios from "axios";
import config from "../config";
import { GetToken, CheckAuth } from "../hooks/Auth";
import Film from "../components/Film";
import { Link as RLink } from "react-router-dom";

function Catalog() {
	const auth = CheckAuth();

	const [HisLoading, setHIsLoading] = React.useState<boolean>(true);
	const [FisLoading, setFIsLoading] = React.useState<boolean>(true);
	const [favorite, setFavorite] = React.useState([]);
	const [history, setHistory] = React.useState([]);

	React.useEffect(() => {
		setFIsLoading(true);
		setHIsLoading(true);
		const getHistory = async () => {
			try {
				const { data: res } = await axios.get(`${config.ks_api}/history/list`, {
					headers: {
						Authorization: `Bearer ${GetToken()}`,
					},
				});
				setHistory(res.history);
			} finally {
				setHIsLoading(false);
			}
		};
		const getFavorite = async () => {
			try {
				const { data: res } = await axios.get(
					`${config.ks_api}/favorite/list`,
					{
						headers: {
							Authorization: `Bearer ${GetToken()}`,
						},
					}
				);
				setFavorite(res.favorite);
			} finally {
				setFIsLoading(false);
			}
		};

		getHistory();
		getFavorite();
	}, []);

	const NullInfo = ({ type }: { type: "history" | "favorite" }) => (
		<Center>
			<Stack direction="column" spacing="20px">
				<Center>
					<Heading size={["sm", "lg"]}>Тут пока пусто</Heading>
				</Center>
				<Center color="gray.400">
					{(type === "favorite" && (
						<Text fontSize={["10px", "md"]}>
							Добавляйте любимые фильмы в избранное, и они появятся тут
						</Text>
					)) || (
						<Text fontSize={["10px", "md"]}>
							Смотрите фильмы, и история просмотра появятся тут
						</Text>
					)}
				</Center>
				{!auth && (
					<Center w={["100%", "auto"]}>
						<Button
							w={["100%", "auto"]}
							colorScheme="brand"
							size={"md"}
							as={RLink}
							to="/login"
						>
							Войти
						</Button>
					</Center>
				)}
			</Stack>
		</Center>
	);

	const Films = ({ list }: any) => (
		<SimpleGrid columns={[2, 3, 4, 5]} spacing="20px">
			{list.map((film: any) => (
				<Film film={film} />
			))}
		</SimpleGrid>
	);

	return (
		<Container maxW="4xl" paddingTop={10}>
			<Tabs variant="soft-rounded" colorScheme="brand">
				<Center>
					<TabList>
						<Tab>Избранное</Tab>
						<Tab>История</Tab>
					</TabList>
				</Center>

				<TabPanels paddingTop={10}>
					<TabPanel>
						{(FisLoading && (
							<Center>
								<Spinner color="brand.500" size="xl" />
							</Center>
						)) || (
							<>
								{(favorite.length === 0 && <NullInfo type="favorite" />) || (
									<Films list={favorite} />
								)}
							</>
						)}
					</TabPanel>
					<TabPanel>
						{(HisLoading && (
							<Center>
								<Spinner color="brand.500" size="xl" />
							</Center>
						)) || (
							<>
								{(history.length === 0 && <NullInfo type="history" />) || (
									<Films list={history} />
								)}
							</>
						)}
					</TabPanel>
				</TabPanels>
			</Tabs>
		</Container>
	);
}

export default Catalog;
